import request from '../util/request'

// 个人资料
export const getProfile = params =>
  request({
    url: 'v1_0/user/profile',
    needToken: true,
    params: params
  })

export const followUser = target =>
  request({
    url: 'v1_0/user/followings',
    method: 'POST',
    needToken: true,
    data: {
      target
    }
  })
// 取消关注用户
export const cancelFollowUser = target =>
  request({
    url: `v1_0/user/followings/${target}`,
    method: 'DELETE',
    needToken: true
  })
// 用户个人信息
export const getInfo = () => {
  return request({
    url: 'v1_0/user',
    method: 'GET',
    needToken: true
  })
}
// 编辑页面信息
export function edtiInfo (data) {
  return request({
    url: 'v1_0/user/profile',
    method: 'PATCH',
    needToken: true,
    data: data
  })
}

// 上传头像
export function edtifile (fd) {
  return request({
    url: 'v1_0/user/photo',
    method: 'PATCH',
    needToken: true,
    data: fd
  })
}

export const block = target => {
  return request({
    url: '/v1_0/user/blacklists',
    data: {
      target
    },
    method: 'POST',
    needToken: true
  })
}
