// 时间格式化
export const newDate = dateTime => {
  const date = new Date(dateTime) // 转换成Data();
  console.log('返回的标准未格式化时间：', date)
  var y = date.getFullYear()
  console.log('年：', y)
  var m = date.getMonth() + 1
  m = m < 10 ? '0' + m : m
  var d = date.getDate()
  d = d < 10 ? '0' + d : d
  return y + '-' + m + '-' + d
}
